












/*------ VARIABLES --------*/
html {
  --accent: #ffbdb8;
  --accent-hover: #ffbdb8;
  --text-dark: #000000;
  --text-light: #ffffff;
  --text-secondary: #b49190;
}
/*-------- GLOBAL ---------*/
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
html {
  height: 100%;
  font-size: 10px;
}
body {
  margin: 0;
  height: 100%;
  min-width: 320px;
  font-family: "Karla", Arial, sans-serif;
  font-size: 2rem;
  line-height: 1.5;
  color: var(--text-dark);
  background-color: #fff;
  overflow-x: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}
ul, ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
img,
video,
audio {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  color: inherit;
}
.container {
  height: 67rem;
  max-width: 37.5rem;
	width: 100%;
}

.login {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-item-align: center;
      align-self: center;
  padding: 5rem 2rem 4rem;
  background: #fed3d0 url(/static/img/bg-start.ff6d88e.jpg) no-repeat center / cover;
}
.login__logo {
  display: block;
  text-align: center;
}
.logo__img {
  width: 100px;
  height: 124px;
}
.logo__caption {
  font-size: 4.5rem;
}
.form__input-wrapper {
  padding: 3rem;
  background-color: #fff;
}
.form__label {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text-secondary);
  letter-spacing: 2px;
  text-transform: uppercase;
}
.form__text-input {
  background: none;
  border: 0;
  display: block;
  width: 100%;
  font-size: 2rem;
  padding: 1.7rem 0 2.5rem;
  border-bottom: 2px solid rgba(0, 0, 0, 0.5);
  outline: none;
  -webkit-transition: .3s;
  transition: .3s;
}
.form__text-input:focus {
  border-color: var(--accent);
}
.form__btn {
  background: none;
  border: 0;
  text-align: left;
  width: 100%;
  background-color: var(--accent);
  font-weight: 700;
  font-size: 1.6rem;
  padding: 2.5rem 3rem;
  position: relative;
  cursor: pointer;
  outline: none;
  -webkit-transition: .3s;
  transition: .3s;
}
.form__btn:hover,
.form__btn:focus {
  background-color: var(--accent);
}
.form__btn:after {
  content: '';
  position: absolute;
  top: 50%;
  right: 3rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  display: block;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 11px solid currentColor;
}

.chat {	
	height: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	background: var(--accent) url(/static/img/bg-chat.1639171.jpg) no-repeat center / cover;
}
.chat__header {
  height: 6.5rem;
  background-color: #000;
}
.navbar {
	color: var(--text-light);
	height: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	padding: 3rem 2rem 1.5rem;
}
.navbar__go-back {
	background: none;
	border: 0;
	display: block;
	width: 40px;
	height: 40px;
	position: relative;
	cursor: pointer;
	color: var(--text-light);
	outline: none;
}
.navbar__go-back:before {
	content: '';
	position: absolute;
	top: 15px;
	left: 10px;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: 11px solid currentColor;
	-webkit-transition: .2s;
	transition: .2s;
}
.navbar__go-back:hover:before,
.navbar__go-back:focus:before {
	border-right: 11px solid var(--accent);
}
.navbar__heading {
	font-size: 2rem;
	font-weight: 400;
}
.chat__body {
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	overflow: auto;
}
.chat__messages {
	padding: 3rem 2rem;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: end;
	    -ms-flex-align: end;
	        align-items: flex-end;
}
.message {
	display: block;
	width: 200px;
	padding: 2rem 1rem 1.5rem 2rem;
	background-color: #fff;
	-webkit-box-shadow: 0 40px 50px rgba(0, 0, 0, 0.2);
	        box-shadow: 0 40px 50px rgba(0, 0, 0, 0.2);
	position: relative;
}
.message:not(:last-child) {
	margin-bottom: 3rem;
}
.message__author {
	display: block;
	position: absolute;
	left: -2rem;
	top: -2rem;
	padding: 1rem;
	font-size: 1.5rem;
	font-weight: 700;
	line-height: 1;
	-webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
	        box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
	background-color: #fff;
}
.message__text {
	font-size: 1.8rem;
	word-wrap: break-word;
}
.message__time {
	margin-top: 2rem;
	font-size: 1.2rem;
	color: var(--text-secondary);
}
::-webkit-scrollbar {
  width: 2px;
}
::-webkit-scrollbar-track {
  background: #ccc;
}
::-webkit-scrollbar-thumb {
  background: #888;
}

.add-message__form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.add-message__input {
  background: none;
  border: 0;
  font-size: 1.8rem;
  color: var(--text-light);
  padding: 2rem;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  outline: none;
}
.add-message__input::-webkit-input-placeholder {
  font-size: 1.8rem;
  color: currentColor;
}
.add-message__input:-ms-input-placeholder {
  font-size: 1.8rem;
  color: currentColor;
}
.add-message__input::-ms-input-placeholder {
  font-size: 1.8rem;
  color: currentColor;
}
.add-message__input::placeholder {
  font-size: 1.8rem;
  color: currentColor;
}
.add-message__btn {
  background: none;
  border: 0;
  font-size: 1.6rem;
  font-weight: 700;
  color: #fcbab5;
  padding: 1rem 2rem;
  cursor: pointer;
  outline: none;
  -webkit-transition: .2s;
  transition: .2s;
}
.add-message__btn:hover,
.add-message__btn:focus {
  color: var(--text-light);
}
